// resets {{{
//==============================================================================
//                              Resets
//==============================================================================

*,
*::before,
*::after
  box-sizing: border-box // Include borders & padding with size

body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd
  margin: 0

ul[role='list'],
ol[role='list']
  list-style: none

// :root:focus-within
//   scroll-behavior: smooth

  :root
  scroll-behavior: smooth

body
  min-height: 100vh
  text-rendering: optimizeSpeed
  font-size: 16px
  line-height: 1.6
  @media (min-width: $desktop)
    font-size: 18px

h1, h2, h3
  line-height: 1.1

picture,
img
  max-width: 100%
  height: auto
  display: block

input,
button,
textarea,
select
  font: inherit

@media (prefers-reduced-motion: reduce)
  html:focus-within
    scroll-behavior: auto

  *,
  *::before,
  *::after
    animation-duration: 0.01ms !important
    animation-iteration-count: 1 !important
    transition-duration: 0.01ms !important
    scroll-behavior: auto !important

header
  padding: 0.5rem

// section
//   padding: 2rem


a
  text-decoration: none

// h1, h2, h3, p
