// }}}
// utils{{{
// border_top{{{
.border_top
  border-top: 1px solid #ccc
  margin: 0 1.5rem
// }}}
// center{{{
.center
  // display: grid
  // place-items: center

  display: flex
  flex-direction: column
  justify-content: center
  align-items: center
// }}}
// underline{{{

.underline
  &:after
    content: ""
    display: block
    height: 3px

    transition: transform 500ms
    transform: scaleX(0)

    background: $primary

  &:hover:after
    transform: scaleX(1)

// }}}
// active{{{
.active
  color: $primary
// }}}
// fades{{{
.faded_out
  opacity: 0
  transform: translateY(25%)
  transition: opacity 500ms, transform 500ms
  // transition: opacity 500ms ease 250ms, transform 500ms ease 250ms
.fade_in
  opacity: 1
  transform: translateY(0)


// }}}
// card{{{
.card
  overflow: hidden
  box-shadow: $shadow
  border-radius: 10px
// }}}
// scroll{{{
.scroll
  animation: scroll 25s linear 0s infinite normal forwards
  &:hover
    animation-play-state: paused

@keyframes #{scroll}
  0%
    transform: translateX(0)
  100%
    transform: translateX(calc(-1 * #{$slide_width} * #{$slide_count_total} + 4rem))

// }}}

// }}}
// components{{{
//==============================================================================
//                              Components
//==============================================================================
// preloader{{{
.preloader
  position: fixed
  z-index: 999
  width: 100vw
  height: 100vh
  background: white
  opacity: 1
  transition: opacity 1s, visibility 0s 2s

  display: grid
  place-items: center

  img
    width: 150px
    @media (min-width: $desktop)
      width: 3j0px

    animation: pulse 2s linear infinite alternate

@keyframes #{pulse}
  0%
    transform: scale(0)
  100%
    transform: scale(1)

.disappear
  opacity: 0
  visibility: hidden

// }}}
// menu button{{{
#menu_button
  position: absolute

  top: 10px
  right: 10px

  z-index: 99
  cursor: pointer

  padding: 5px 10px
  padding-bottom: 20px

  @media (min-width: $desktop)
    display: none

#bar
  position: relative
  transition: background 500ms

#bar, #bar:after, #bar:before
  width: 30px
  height: 3px
  background: #333
  border-radius: 50px

#bar:after, #bar:before
  content: ""
  display: block
  position: absolute
  transition: transform 500ms

#bar:before
  top: 7px

#bar:after
  top: 15px
// }}}
// menu{{{
#menu
  position: absolute
  top: 0

  height: 100vh
  width: 100vw

  transform: scale(0)
  transition: transform 500ms

  display: flex
  flex-direction: column
  justify-content: center
  align-items: center

  @media (min-width: $desktop)
    all: unset
    display: flex
    > * + *
      margin-left: 2rem

  background: $navbar_background
// }}}
// logo{{{
#logo
  display: block
  img
    width: 120px
    transition: transform 500ms
    &:hover
      transform: scale(1.3)
    @media (min-width: $desktop)
      width: 150px
// }}}
// toggler{{{
#toggler
  display: none
  &:checked ~ #menu
    transform: scale(1)
  &:checked ~ label #menu_button #bar
    background: none
    margin-top: 7px
    &:before,
    &:after
      top: 0
    &:before
      transform: rotate(45deg)
    &:after
      transform: rotate(-45deg)
// }}}
// menu_header{{{
.menu_header
  // display: inline-block

  text-align: center
  margin: 2rem 0
  @media (min-width: $desktop)
    margin: 4rem 0

  font-family: $font_display
  font-size: 1.5rem
  @media (min-width: $desktop)
    font-size: 2.5rem
    font-weight: bold

  // background: $fg_dark_title
  // color: $fg_dark_title

  // &:after
  //   content: ""
  //   display: block
  //   width: 50px
  //   margin: 0 auto
  //   height: 3px

// }}}
// mail{{{
#mail
  border: 1px solid black
    margin: 2rem 0
    padding: 1rem

    font-weight: bold
// }}}
// slider{{{
.slider
  width: $slider_width
  overflow-x: hidden
  margin-top: 3rem

  .track
    display: flex
    align-items: center
    & > * + *
      margin-left: 1rem
      @media (min-width: $desktop)
        margin-left: 3rem
    & > *
      flex: 1 0 auto
    .icon
      width: 100%
      max-width: $slide_width
      max-height: $slider_height
// }}}
// hero{{{
.hero
  height: 100vh
  position: relative
  overflow: hidden

  > *
    position: absolute

  // &__image,
  &__items,
  img
    min-width: 100%
    min-height: 100%

  &__items

    display: flex
    flex-direction: column
    justify-content: center
    align-items: center
    padding: 1rem

    background: rgba(black, 0.7)
    h1
      color: $primary
    p
      color: $fg_light_text


    > * + *
      margin-top: 1rem

  img
    object-fit: cover

  // .hero__image
  //   img
  //     object-fit: cover

// }}}
// description{{{
.description
  padding: 2rem
  display: grid
  place-items: center
// }}}
// link{{{
.link
  text-transform: capitalize

  font-size: 2rem
  @media (min-width: $desktop)
    font-size: 1rem

  // color: $yy_fg
  color: $fg_dark_text
  transition: color 500ms
  &:hover
    color: $primary
// }}}
// icon{{{
.icon
  display: inline-block

  transition: transform 500ms
  &:hover
    transform: scale(1.2)
// }}}

