// exp{{{
@keyframes #{reel_right}
  0%
    transform: translateX(-100%)
  100%
    transform: translateX(0)
@keyframes #{reel_left}
  0%
    transform: translateX(100%)
  100%
    transform: translateX(0)

.reel_right_1
  animation: reel_right 1s linear calc(var(--scroll) * -2.3s) 1 normal both paused

.reel_left
  animation: reel_left 1s linear calc(var(--scroll) * -1.8s) 1 normal both paused

.reel_right_2
  animation: reel_right 1s linear calc(var(--scroll) * -1.8s) 1 normal both paused


